"use strict"


/**
 *  @param {string} time 
 *  @param {bool} friendly fromNow
 */
exports.getLastTimeStr = function(time, friendly) {
    if (friendly) {
        return utils.MillisecondToDate(new Date() - new Date(time));
    } else {
        return utils.fmtDate(new Date(time),'yyyy-MM-dd hh:mm');
    }
}

/** 
 *  @param {string} tab Tab
 *  @param {bool} good 
 *  @param {bool} top 
 */
exports.getTabStr = function(tab, good, top) {
    let str = "";
    if (top) {
        str = "";
    } else if (good) {
        str = "";
    } else {
        switch (tab) {
            case "share":
                str = "";
                break;
            case "ask":
                str = "";
                break;
            case "job":
                str = "";
                break;
            default:
                str = "";
                break;
        }
    }
    return str;
}

/** 
 *  @param {string} tab Tab
 *  @param {bool} good 
 *  @param {bool} top 
 */
exports.getTabClassName = function(tab, good, top) {
    let className = '';

    if (top) {
        className = "top";
    } else if (good) {
        className = "good";
    } else {
        switch (tab) {
            case "share":
                className = "share";
                break;
            case "ask":
                className = "ask";
                break;
            case "job":
                className = "job";
                break;
            default:
                className = "default";
                break;
        }
    }
    return className;
}

/** title
 *  @param {string} tab Tab
 */
exports.getTitleStr = function(tab) {
    let str = "";
    switch (tab) {
        case "share":
            str = "";
            break;
        case "ask":
            str = "";
            break;
        case "job":
            str = "";
            break;
        case "good":
            str = "";
            break;
        default:
            str = "";
            break;
    }
    return str;
}



